'use client'
import { Button } from "@/components/ui/button";
import * as XLSX from 'xlsx';

export default function ConsultasPage() {
  const consultas = Array.from({ length: 10 }).map((_, i) => ({
    id: i + 1,
    nombre: `Consulta ${i + 1}`,
    fecha: new Date().toLocaleDateString(),
  }));

  const handleDownload = () => {
    const worksheet = XLSX.utils.json_to_sheet(consultas);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Consultas");
    XLSX.writeFile(workbook, "consultas.xlsx");
  };

  return (
    <div className="container mx-auto py-10">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">Consultas</h1>
        <Button onClick={handleDownload}>Descargar Excel</Button>
      </div>
      <div className="border rounded-lg">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">ID</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Nombre</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Fecha</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {consultas.map((consulta) => (
              <tr key={consulta.id}>
                <td className="px-6 py-4 whitespace-nowrap">{consulta.id}</td>
                <td className="px-6 py-4 whitespace-nowrap">{consulta.nombre}</td>
                <td className="px-6 py-4 whitespace-nowrap">{consulta.fecha}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
